import React, { Component } from 'react';
import {BrowserRouter as Router,Route} from 'react-router-dom';
import Todos from './components/Todos';
import Header from './components/layouts/header';
import AddTodo from './components/AddTodo';
import './App.css';
import uuid from 'uuid';
import About from "./components/pages/about";
import axios from 'axios';

class App extends Component {
  // state = {
  //   todos : [
  //     {
  //       id : uuid.v4(),
  //       title : "Make morning tea",
  //       completed : true
        
  //     },
  //     {
  //       id : uuid.v4(),
  //       title : "Grocery shopping",
  //       completed : false       
  //     },
  //     {
  //       id : uuid.v4(),
  //       title : "Go to Sammamish High School",
  //       completed : false
        
  //     }
  //   ]
  // } 

  state = {
    todos : []
  }

  componentDidMount() {
    //axios.get('https://jsonplaceholder.typicode.com/todos?_limit=25').then(y => console.log(y.data))
    axios.get('https://jsonplaceholder.typicode.com/todos?_limit=25').then(y => this.setState({todos: y.data}))
  }

  markComplete = (id) =>{
    console.log("mark complete in App.js called:" + id);
    this.setState({
      todos: this.state.todos.map(x => {
        if (x.id === id)
        {
          x.completed = !x.completed;
        }
        return x;
      })
    });
  }

  deleteTodoItem = (id) =>{
    console.log("Delete " + id);
    axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`)
    .then(y => this.setState({todos : [...this.state.todos.filter(k => k.id!==id)]}));
    // this.setState({
    //   todos : [...this.state.todos.filter(x => x.id !== id)]      
    // });   
  }

  addTodo= (x) => {
      console.log(x);
      // const newTodo = {
      //   id : uuid.v4(),
      //   title : x,
      //   completed: false
      // }
      // this.setState({todos : [...this.state.todos, newTodo]})
      axios.post("https://jsonplaceholder.typicode.com/todos",{
        title:x,
        completed:false
      }).then(y => this.setState({todos : [...this.state.todos,y.data]}))
  }

  render() {   
    return (
      <Router>
        <div className="App">
          <h1>My React Application</h1>
          <h2>01/20/2019</h2>
          <Header></Header>
          <Route exact path="/" render = {
            props => (
              <React.Fragment>
                  <AddTodo addTodo = {this.addTodo}></AddTodo> 
                  <Todos todos = {this.state.todos} markComplete={this.markComplete} deleteTodoItem={this.deleteTodoItem}></Todos>    
              </React.Fragment>
            )
          } >
          </Route>
          <Route path="/about" component={About}>

          </Route>
            
        </div>
      </Router>
      
      
    );
  }
}

export default App;
