import React, { Component } from 'react';
import PropTypes from 'prop-types';

class TodoItem extends Component {
    getStyle = () =>{
        
        /*
        if (this.props.todoitem.completed){
            return {
                textDecoration : 'line-through',
                backgroundColor : '#F4F4F4'
            }
        }
        else {
            return {
                textDecoration : 'none',
                backgroundColor : '#F4F4F4'
            }
        }
        */

        return {
            textDecoration : this.props.todoitem.completed ? 'line-through' : 'none',
            backgroundColor : '#F4F4F4',
            padding : '10px',
            borderBottom: '1px solid #CCCCCC',
            minWidth: '350px'
        }
    }   

    render() {
        const {id, title,completed} = this.props.todoitem;
        return(
            <div style={this.getStyle()}>
            <input style={{display:'inline'}} type="checkbox" checked={completed} onChange={this.props.markComplete.bind(this,id)}></input>        
            <p style={paraStyle}>{title}</p>
            <button style={btnStyle} onClick={this.props.deleteTodoItem.bind(this,id)}>x</button>
            </div>
        )     
    }
}   

 //PropTypes
 TodoItem.propTypes = {
    todoitem : PropTypes.object.isRequired
}

//Not in use
const itemDivStyle = {
    backgroundColor : '#F4F4F4',
    minWidth : '250px'
    
}

const paraStyle = {
    margin:0,
    display: 'inline',
    paddingLeft:'5px'
}

const btnStyle = {
    backgroundColor: '#FF0000',
    color: '#FFFFFF',
    border: 'none',
    padding:'10px, 10px',
    cursor: 'pointer',
    float: 'right',
    marginRight: '15px'
}

 //PropTypes
 TodoItem.propTypes = {
    todoitem : PropTypes.object.isRequired,
    markComplete : PropTypes.func.isRequired,
    deleteTodoItem : PropTypes.func.isRequired
}

export default TodoItem;