import React from 'react';
import {Link} from 'react-router-dom';

function Header(){
    return (
        <header style={headerStyle}>
            <p>Todo List</p>
            <div style={navDiv}>
                <span style={linkSpan}><Link to="/" style={linkItem}>Home</Link></span>| <span style={linkSpan}><Link to="/about" style={linkItem}>About</Link></span>
            </div>
            
        </header>
    )
}

export default Header;

const headerStyle = {
    color: '#FFFFFF',
    backgroundColor: '#333333',
    padding: '5px',
    textAlign : 'center',
    fontFamily : 'Segoe UI',
    fontSize: 25
}

const navDiv = {
    fontSize:16,    
}

const linkSpan = {
    padding:'2px'
}

const linkItem = {
    color:'#FFFFFF'
}